@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&family=Pathway+Extreme:ital,opsz,wght@0,8..144,100..900;1,8..144,100..900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
}

// Color Variables

$color1: #FFFFFF;
$color2: #6610A6;
$color3: #9E4BDC;
$color4: #03b0c1;
$font-clr1: #f2f2f2;
$font-clr2: #848484;
$font-clr2: #A539AD;
$btn-clr1: #A101A2;

// Font Styles

$font1: 'Pathway Extreme', sans-serif;
$font2: Montserrat, sans-serif;

body {
    width: 100%;      
    height: 100vh;     
    display: flex;
    flex-direction: column;
}


// Style for Header go here

#header {
    flex: 0 1 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(to right, $color1, $color2);
    font-family: $font1;
    padding: 0 1rem;

    div {
        background: linear-gradient(to right, $color2, $color4);
        background-clip: text;
        h1 {
            padding: 0.5rem 1rem;
            color: transparent;
            font-size: 1.5rem;
        }
    }

    .material-symbols-outlined.account-icon {
        font-size: 3rem;
        padding: 0.5rem;
        background: linear-gradient(to right, $color1, #333);
        background-clip: text;
        position: relative;
        color: transparent;

        &::after {
            content: "";
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            position: absolute;
            background: linear-gradient(to right, $color2, $color4);
            z-index: -1;
        }

    }

}


#main {
    flex: 1 1 auto;
    width: 100%;
    height: 100%;
    font-family: $font1;

    .form-wrap {
        display: grid;
        grid-template-columns: auto 1.3fr;
        grid-template-rows: auto;
        gap: 0px 0px;
        grid-template-areas: 
        "some-nav some-form";
        width: 100%;
        padding: 2rem;
        height: max-content;

        .some-nav {
            grid-area: some-nav;
            list-style: none;
            padding: 0;
            border: 1px solid #333;

            li {
                
                padding: 1.5rem 2rem;

                a {
                    text-decoration: none;
                }

            }

        }

        #some-form {
            padding: 2rem;
            border: 1px solid #333;
            grid-area: some-form;
            display: grid; 
            grid-template-columns: 1fr; 
            grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr; 
            gap: 0px 0px; 
            grid-template-areas: 
            "."
            "."
            "."
            "."
            "."
            "."
            "."
            "."; 

            .detes-grid {
                display: flex;
                justify-content: space-evenly;
                align-items: center;

                .form-group {
                    flex: 1 1 auto;

                    input {
                        width: 100%;
                        margin: 0.1rem 1rem;
                        padding: 0.1rem 1rem;
                    }

                }

            }

        }

    }

}


#footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5em;
    font-family: $font2, sans-serif;
    background: $color4;
    flex: 0 1 auto;

    div {
        display: flex;
        align-items: center;

        h3 {
            margin: 0 1rem;
        }
    }

    ul {
        display: flex;
        list-style: none;

        li {

            i {
                margin: 0 1rem;
                cursor: pointer;
            }

        }

    }

}